import { customElement, property } from 'lit/decorators.js';
import { classMap } from 'lit/directives/class-map.js';
import { html, TemplateResult } from 'lit';

import { Base } from '../base';

export type IPromise = 'promise' | 'social' | 'none';

@customElement('zwc-promise')
export class ZwcPromise extends Base {
  @property({ type: String })
  title = 'Green';

  @property({ type: String })
  subtitle = 'Promise';

  @property({ type: String })
  promise: IPromise = 'none';

  @property({ type: Boolean })
  inverted = false;

  render(): TemplateResult {
    const classes = {
      'promise-label': true,
      'promise-label--inverted': this.inverted,
      'promise-label--social': this.promise === 'social',
    };

    const promise =
      this.promise !== 'none'
        ? html`
            <a class=${classMap(classes)} href="#">
              <span class="promise-label-title">${this.title}</span>
              <span class="promise-label-subtitle">${this.subtitle}</span>
            </a>
          `
        : '';

    return html`${promise}`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-promise': ZwcPromise;
  }
}
